	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Wanna buy some slaves on ebay?","date":"09 Dec 2004","slug":"/2004-12-09-wanna-buy-some-slaves-on-ebay/","tags":"Y2004"},"html":"<p><a title=\"vowe dot net :: Weird sponsored links\" href=\"http://vowe.net/archives/005361.html\">vowe: Wanna buy some slaves on ebay?</a></p>"},"next":{"fields":{"title":"Truly, Deeply, Madly","date":"09 Dec 2004","slug":"/2004-12-09-truly-deeply-madly/"}},"previous":{"fields":{"title":"stopping bullets with your teeth","date":"10 Dec 2004","slug":"/2004-12-10-stopping-bullets-with-your-teeth/"}}},{"node":{"fields":{"title":"Truly, Deeply, Madly","date":"09 Dec 2004","slug":"/2004-12-09-truly-deeply-madly/","tags":"Y2004"},"html":"<p>Ever since we first met, we have been very successful making sudden, impulsive decisions and letting fate take us where it may. Pledging to marry while on separate continents, dropping out of grad school, leaving Minneapolis without the promise of a job in Phoenix, leaving everything we owned to move to Germany, jumping head first into the IT boom in the late '90s, having our first child after 15 years of happy, peaceful marriage, jumping back into a career after a year of baby leave... these were all things we decided very quickly and suddenly to make happen or let happen to us.</p>\n<p>We're on the verge of another such impulsive decision or twist of fate. If you've read the weblog for  long, you might guess what it is.... more details once things are final. However, right now I'm just ecstatic to find that at my age, impulsive decisions can still happen.</p>"},"next":{"fields":{"title":"Strongly is not strong enough","date":"09 Dec 2004","slug":"/2004-12-09-strongly-is-not-strong-enough/"}},"previous":{"fields":{"title":"Wanna buy some slaves on ebay?","date":"09 Dec 2004","slug":"/2004-12-09-wanna-buy-some-slaves-on-ebay/"}}},{"node":{"fields":{"title":"Strongly is not strong enough","date":"09 Dec 2004","slug":"/2004-12-09-strongly-is-not-strong-enough/","tags":"Y2004"},"html":"<blockquote>The corporation insists on an elaborate aptitude test for new employees that is intended to weed out troublemakers. When Barbara Ehrenreich took the test at the Minneapolis Wal-Mart, she was told that she had given a wrong answer when she agreed \"strongly\" with the proposition that \"rules have to be followed to the letter at all times.\" The only acceptable answer for Wal-Mart was \"very strongly.\" Similarly, the only correct answer to the proposition \"there is room in every corporation for a non-conformist\" was: \"totally disagree.\"</p></blockquote>\n<p><a title=\"The New York Review of Books: Inside the Leviathan\" href=\"http://www.nybooks.com/articles/17647\">The New York Review of Books: Inside the Leviathan</a>, an essay on WalMart. <a title=\"Davos Newbies : Davos Newbies Home\" href=\"http://www.davosnewbies.com/2004/12/07#theWalmartModel\">(via Davos Newbies)</a></p>"},"next":{"fields":{"title":"Sock Puppet","date":"09 Dec 2004","slug":"/2004-12-09-sock-puppet/"}},"previous":{"fields":{"title":"Truly, Deeply, Madly","date":"09 Dec 2004","slug":"/2004-12-09-truly-deeply-madly/"}}}],"pathPrefix":"","first":false,"last":false,"index":368,"pageCount":1101}}

